'use client'

import BottomNavigationBar from "@/components/navigation/BottomNavigationBar";
import TopNavigationBar from "@/components/navigation/TopNavigationBar";
import { Toaster } from "@/components/ui/toaster";
import { Stack } from "@buildo/bento-design-system";
import { FollowerPointerCard } from "@/components/ui/following-pointer";
import { Modal, Body } from '@buildo/bento-design-system'
import useDeviceContext from "@/hooks/useDeviceContext";
import MissionControl from "@/components/mission-control";
import { Metadata } from 'next';
import { useState, useEffect } from 'react'
// export const metadata: Metadata = {
//   title: 'ROSARIO',
//   description: 'Fine-tuning coffee experience',
// };

function iOS() {
  return [
    'iPad Simulator',
    'iPhone Simulator',
    'iPod Simulator',
    'iPad',
    'iPhone',
    'iPod'
  ].includes(navigator.platform)
    // iPad on iOS 13 detection
    || (navigator.userAgent.includes("Mac") && "ontouchend" in document)
}


const Layout = ({ children }) => {
  const { modalDisplay, setModalDisplay } = useDeviceContext();
  const [incompatibleModal, setIncompatibleModal] = useState(false)

  useEffect(() => {
    if (!!!navigator.bluetooth) {
      setIncompatibleModal(true)
    }
  }, [])


  if (modalDisplay) {
    return (
      <div className="w-screen h-screen  z-50">
        <MissionControl />
      </div>
    )
  }

  return (
    <>
      {
        incompatibleModal && (
          <Modal
            kind="destructive"
            title="Incompatible browser"
            onClose={() => setIncompatibleModal(false)}
            primaryAction={{
              label: "OK",
              onPress: () => {
                setIncompatibleModal(false);
                // onChange(powe
              },
            }}
          >
            <Body size="large">{"This browser does not come with Bluetooth functionality to connect with ROSARIO. "}</Body>
            {iOS() ? (
              <Body size="large">{"Please download and use BlueFy browser from App Store instead"}</Body>
            ) : (
              <Body size="large">{"Please use Microsoft Edge or latest version of Chrome browser"}</Body>
            )}
          </Modal>
        )
      }

      <Stack space={4}>
        <TopNavigationBar />
        <div className="mt-[48px] overflow-auto" style={{ height: 'calc(100vh - 48px - 56px )' }}>
          {
            modalDisplay ? (<MissionControl />) : children
          }</div>
        <Toaster />

        <BottomNavigationBar />
      </Stack>


    </>
  )

};

export default Layout;
